@media only screen and (min-width: 701px) {
  .order-product {
    display: flex;
    margin-right: 65px;
  }
  .order-product:last-of-type {
    margin-right: 0;
  }
  .order-product-image {
    width: 150px;
    max-height: 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 20px;
  }
}

@media only screen and (max-width: 700px) {
  .order-product {
    display: flex;
    margin-bottom: 50px;
  }
  .order-product-image {
    width: 150px;
    max-height: 150px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 20px;
  }
}

.order-product-image img {
  width: 100%;
}

.order-product-info {
  display: flex;
  flex-direction: column;
  width: 200px;
}

.order-product-price {
  margin: 10px 0;
}
